@media only screen and (max-width: 600px) {
  .embed-container {
    position: relative;
    padding-bottom: 57.5%;
    height: 0;
    overflow: hidden;
  }
}

@media only screen and (min-width: 601px) {
  .embed-container {
    position: relative;
    padding-bottom: 52.1%;
    height: 0;
    overflow: hidden;
  }
}
.embed-container iframe,
.embed-container object,
.embed-container embed {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}

.font {
  font-family: "Open Sans", sans-serif;
}
