/* FIM GLITCH */

.button::before {
  content: "";
  width: 100%;
  height: 100%;

  position: absolute;

  background: #050c1b;

  transition: all 1s;
  z-index: -1;
}

.item3::before {
  width: 100%;
  height: 0%;
  color: white;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%) rotate(-45deg);
}

.item3:hover::before {
  height: 375%;
}

.pk-loader {
  background: #050c1b;
  bottom: 0;
  left: 0;
  position: fixed;
  right: 0;
  top: 0;
  z-index: 99999;
}
.pk-loader svg {
  bottom: 0;
  height: 70px;
  left: 0;
  margin: auto;
  position: absolute;
  right: 0;
  top: 0;
  width: 70px;
}
.pk-loader svg polygon {
  stroke-dasharray: 34;
  -webkit-animation: dash 2.5s cubic-bezier(0.35, 0.04, 0.63, 0.95) infinite;
  animation: dash 2.5s cubic-bezier(0.35, 0.04, 0.63, 0.95) infinite;
}
.loader-text {
  color: #4f46e5;
  left: 0;
  letter-spacing: 4px;
  margin-top: 50px;
  position: absolute;
  right: 0;
  text-align: center;
  top: 50%;
  transform: translateY(-50%);
}
@-webkit-keyframes dash {
  to {
    stroke-dashoffset: 136;
  }
}
@keyframes dash {
  to {
    stroke-dashoffset: 136;
  }
}
